	import {
		debounce
	} from '@/common/utils.js';
export default {
	data() {
		return {
			n: true,
			contract: [{}, {}],
			hashrate_total: '0',
			use_avg: '0',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
			cunli: true,
			categart: '',
			shade:false,
			chnerot:''
		};
	},
	onShow() {
		var that = this;
		uni.request({
			url: this.url + 'usercloudagree/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + uni.getStorageSync('token')
			},
			success(res) {
				console.log(res)
				that.chnerot = res.data.data.user_agreement;
				//that.agreement = res.data.data.agreement;
				if (that.chnerot == 0) {
					that.shade = true;
				}else{
					that.shade = false
				}
			}
		});
		
	},
	onLoad() {
		var that = this;
		uni.request({
			url: this.url + 'myclouds/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				console.log(res);
				var contract = [];
				var hashrate_total = 0; // 总数
				var use_total = 0;
				var use_avg = 0; // 平均数
				if (res.data.data && res.data.data.length) {
					contract = res.data.data.map(item => {
						item.starttime = item.starttime ? item.starttime.substring(0, 10) : '';
						item.endtime = item.endtime ? item.endtime.substring(0, 10) : '';
						hashrate_total += parseFloat(item.hashrate || 0);
						use_total += parseFloat(item.use || 0);
						return item;
					});
					use_avg = (use_total / res.data.data.length).toFixed(2);
				}
				that.hashrate_total = hashrate_total;
				that.use_avg = use_avg;
				that.contract = contract;
				if (that.contract.length != 0) {
					that.cunli = false;
				}
			}
		});
	},
	methods: {
		power_serve(){
			uni.navigateTo({
				url:'../powerAgreement/powerAgreement'
			})
		},
		cancel: function() {
			uni.navigateBack({
				delta:1
			})
		},
		linkToTransfer2: debounce(
			function() {
				uni.navigateTo({
					url: '../powerAgreement/powerAgreement'
				});
			},
			500,
			true
		),
		sure: function() {
			this.linkToTransfer2()
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		backFront:function(){
			console.log(123)
			uni.navigateBack({
				delta:1
			})
		},
		transfer: function(item) {
			var that = this;
			uni.request({
				url: this.url + 'cloudtransfers/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {
					console.log(res);
					if (res.statusCode == 200) {
						uni.navigateTo({
							url: '../power-transfer/power-transfer?ids=' + item.id + '&day=' + item.days + '&rate=' + item.hashrate
						});
					}
					if (res.statusCode == 400) {
						uni.showToast({
							icon: 'none',
							title: '未实名认证通过或未设置资金密码'
						});
					}
				}
			});
		},
		bt: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		bbctn: function() {
			uni.showToast({
				title: '存力转让功能暂未开放',
				icon: 'none',
				duration: 3000
			});
		}
	}
};
